@import url(https://fonts.googleapis.com/css2?family=Overpass&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Overpass&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-image: radial-gradient(#1C2837, #050608);
  background-repeat: no-repeat;
  background-size: cover;
}

.planet-box {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 10px;
    border-style: solid;    
    border-radius: 20px;
    border-color: #1c212c;
    box-shadow: 0 0 50px 50px #1c212c;
    background-color: #1c212c;
    margin: auto;
    position: relative;
}

@-webkit-keyframes textPop {
    from{font-size: .1rem;}
    to{font-size: medium;font-size: initial;}
}

@keyframes textPop {
    from{font-size: .1rem;}
    to{font-size: medium;font-size: initial;}
}

@media screen and (max-width: 480px) {
    .planet-box {
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
        margin-left: auto;
        margin-right: auto;
    }
}

.info-box {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    margin: 20px;
}

.icones {
    width: 20px;
    height: 20px;
    padding: 5px;
    border-style: solid;
    border-color:rgb(100, 149, 237);
    border-radius: 5px;
    border-width: 2px;
    background-color: beige;
}

.planet-text {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    width: 300px;
    color: white;
    font-family: 'Overpass', sans-serif;
    font-size: .8rem;
    text-align: left;
    padding-left: 10px;
    margin-top: auto;
    margin-bottom: auto;
    -webkit-animation: textPop .1s linear;
            animation: textPop .1s linear;
}


.planet-title {
    font-size: 2rem;
    -webkit-animation: textPop .1s linear;
            animation: textPop .1s linear;
}

.hide-box {
    display: none;
}

.close {
    color: white;
    position: absolute;
    top: 15px;
    right: 30px;
    cursor: pointer;
    font-size: 1.8rem;
}

.close:hover{
    color: rgb(100, 149, 237);
}


.satelites{
    color: white;
    width: 90px;
    display: -webkit-flex;
    display: flex;
    grid-gap: 10px;
    gap: 10px;
    position: fixed;
    top: 10%;
    left: 58%;
    -webkit-animation: moons 2s linear;
            animation: moons 2s linear;
}
@media screen and (max-width : 700px){
    .satelites{
        visibility: hidden;
    }
}

@-webkit-keyframes moons {
    0%{top: -10%;}
    50%{top: 0%;}
    to{top: 10%;}
}

@keyframes moons {
    0%{top: -10%;}
    50%{top: 0%;}
    to{top: 10%;}
}

.satelite-img{
    width: 30px;
    height: 30px;
    background-image: url(/solar-system/static/media/moon.0f9b2e9c.svg);
    cursor: pointer;
}

.satelite-img:hover,
.satelite-img:focus{
    border: 3px solid rgb(100, 149, 237);
    border-radius: 50%;
}

.satelite-img:nth-child(2n){
    width: 20px;
    height: 20px;
    background-repeat: no-repeat;
}

.satelite-img:nth-child(3n){
    width: 15px;
    height: 15px;
    background-repeat: no-repeat;
}
.solar-system{
    min-height: 350px;
    max-height: 90vh;
    max-width: 90vh;
    height: 60vw;
    min-width: 350px;
    width: 60vw;
    position: relative;
}

.buttons-speed{
    position: fixed;
    display: -webkit-flex;
    display: flex;
    grid-gap: 10px;
    gap: 10px;
    bottom: 40px;
    right: 50px;
}

.orbit-speed{ 
    cursor: pointer;
    position: relative;
    font-family: 'Overpass', sans-serif;
    font-weight: bold;
    width: 50px;
    outline: none;
    border: none;
    border-radius: 2px;
    font-size: 1rem;
    border: 2px solid #f0ffff;
    background-color: #f0ffff;
    color: #6495ed;
}

.orbit-speed:focus, 
.orbit-speed:hover{ 
    background-color: #6495ed;
    color: white;
}

.star{
    position: fixed;
    border-radius: 50%;
    box-shadow: 0 0 5px 1px #fff;
    height: 2px;
    width: 2px;
    background-color: white;
    z-index: 0;
    opacity: 0.5;
}

.star:nth-child(40n){
    -webkit-animation: lucifer 5s infinite;
            animation: lucifer 5s infinite;
}

.star:nth-child(20n){
    -webkit-animation: lucifer 7s infinite;
            animation: lucifer 7s infinite;
}

@-webkit-keyframes lucifer{
    50%{
        box-shadow: 0 0 5px 4px #fff;
        opacity: .8;}
}

@keyframes lucifer{
    50%{
        box-shadow: 0 0 5px 4px #fff;
        opacity: .8;}
}

.planet{
    border: 1px solid rgb(240, 255, 255, 0.3);
    border-radius: 50%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin-bottom: auto;
    margin-top: auto;
    margin-left: auto;
    margin-right: auto;
    background-color: rgba(0, 0, 0, 0);
    -webkit-animation: orbit infinite linear;
            animation: orbit infinite linear;
}

.planet:hover,
.planet:hover .img-planet{
    cursor: pointer;
    border: 2px solid rgb(100, 149, 237, 1);
}

@-webkit-keyframes orbit{
    from{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    to{-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}

@keyframes orbit{
    from{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    to{-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}

.neptune{
    height: 100%;
    width: 100%;
}

.uranus{
    width: 90%;
    height: 90%;
}

.saturn{
    width: 80%;
    height: 80%;
}

.jupiter{
    width: 70%;
    height: 70%;
}

.mars{
    width: 60%;
    height: 60%;
}

.earth{
    width: 50%;
    height: 50%;
}

.venus{
    width: 40%;
    height: 40%;
}

.mercury{
    width: 30%;
    height: 30%;
}

.sun{
    position: absolute;
    height: 15%;
    width: 15%;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin-bottom: auto;
    margin-top: auto;
    margin-left: auto;
    margin-right: auto;
    background-image: url(/solar-system/static/media/sun.b4e0764d.svg);
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 50%;
}


.img-planet{
    position: relative;
    left: 0;
    right: 0;
    margin: 0px auto;
    border-radius: 50%;
    background-repeat: no-repeat;
}

.img-planet:hover{
    border: 3px solid rgb(100, 149, 237);
    cursor: pointer;
}

.img-neptune{
    height: 20px;
    width: 20px;
    top: -10px;
    background-image: url(/solar-system/static/media/neptune.866047c0.svg);
}

.img-uranus{
    height: 25px;
    width: 25px;
    top: -12.5px;
    background-image: url(/solar-system/static/media/uranus.61f1b8cd.svg);
}

.img-saturn{
    height: 50px;
    width: 50px;
    top: -25px;
    background-image: url(/solar-system/static/media/saturn.0a661e2a.svg);
}

.img-jupiter{
    height: 30px;
    width: 30px;
    top: -15px;
    background-image: url(/solar-system/static/media/jupiter.55fb406c.svg);
}

.img-mars{
    height: 20px;
    width: 20px;
    top: -10px;
    background-image: url(/solar-system/static/media/mars.98dd0195.svg);
}

.img-earth{
    height: 20px;
    width: 20px;
    top: -10px;
    background-image: url(/solar-system/static/media/earth.f54a92eb.svg);
}

.img-venus{
    height: 20px;
    width: 20px;
    top: -10px;
    background-image: url(/solar-system/static/media/venus.ce382351.svg);
}

.img-mercury{
    height: 20px;
    width: 20px;
    top: -10px;
    background-image: url(/solar-system/static/media/mercury.eaa81e73.svg);
}

.orbit-active{
    border: 1px solid rgb(0, 0, 0, 0);
}

.orbit-active:hover{
    border: 1px solid rgb(0, 0, 0, 0);
}

.sun-active{
    opacity: 0;
}

.orbit-active div:not(.clicked){
    opacity: 0;
}

.clicked{
    position: fixed;
    z-index: 10;
    top: 5%;
    margin-top: 0px;
    -webkit-animation: planetSelected 2s ease-in-out;
            animation: planetSelected 2s ease-in-out;
    height: 15vw; 
    width: 15vw;
}
@media screen and (max-width: 700px){
    .clicked{
        max-height: 300px;
        min-height: 200px;
        max-width: 300px;
        min-width: 200px;
    }
}

@-webkit-keyframes planetSelected {
    from{top: 50%; height: 20px; width: 20px;}
    50%{top: 90%;}
    100%{top: 5%; height: 15vw; width: 15vw;}
}

@keyframes planetSelected {
    from{top: 50%; height: 20px; width: 20px;}
    50%{top: 90%;}
    100%{top: 5%; height: 15vw; width: 15vw;}
}

.info{
    visibility: hidden;
}

.info-active{
    position: fixed;
    left: 0;
    right: 0;
    margin: 0px auto;
    top: 28%;
    -webkit-animation: infoBottom 2s ease-in-out;
            animation: infoBottom 2s ease-in-out;
    z-index: 10;
}

@-webkit-keyframes infoBottom {
    from{top: 100%;}
    50%{top: 100%;}
    to{top: 28%;}
}

@keyframes infoBottom {
    from{top: 100%;}
    50%{top: 100%;}
    to{top: 28%;}
}
